<script>
import buttonWithLoading from "../components/layout/ButtonWithLoading";
import inlineActionsTable from "./TuneKeySubscription/RegularTables/InlineActionsTable";
export default {
  components: {
    buttonWithLoading,
    inlineActionsTable,
  },
  data() {
    return {
      result1: false,
      result2: false,
      result3: false,
      result4: false,
      verticalAlign: "top",
      horizontalAlign: "right",
      showTestButton: false,
    };
  },
  methods: {
    start(num) {
      let self = this;
      setTimeout(() => {
        switch (num) {
          case 1:
            self.result1 = true;
            break;
          case 2:
            self.result2 = true;
            break;
          case 3:
            self.result3 = true;
            break;
          case 4:
            self.result4 = true;
            break;
        }
      }, 2000);
    },
    success() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: "Success",
        type: "success",
        timeout: 2000,
      });
      this.result1 = false;
    },
    error() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: "Error",
        type: "error",
        timeout: 2000,
      });
      this.result2 = false;
    },
    warning() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: "Warning",
        type: "warning",
        timeout: 2000,
      });
      this.result3 = false;
    },
    info() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: "Info",
        type: "info",
        timeout: 2000,
      });
      this.result4 = false;
    },
  },
};
</script>
